import { Button, Card, Flex, Heading, Text, useModal } from '@pancakeswap/uikit'
import { useWeb3React } from '@web3-react/core'
import axios from 'axios'

import BigNumber from 'bignumber.js'
import Page from 'components/Layout/Page'
import PageHeader from 'components/PageHeader'
import { useTranslation } from 'contexts/Localization'
import { useDNFTContract, useERC20,  useNftStakingContract } from 'hooks/useContract'
import useToast from 'hooks/useToast'
import { startsWith } from 'lodash'
import React, { useEffect, useCallback, useState, useMemo, useRef } from 'react'
import { Route, useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { getCakeAddress, getDNFTcraneAddress,  getNftStakingcraneAddress } from 'utils/addressHelpers'
import { getDNFTContract, getNftStakingContract } from 'utils/contractHelpers'
import { getBalanceAmount, getBalanceNumber, getDecimalAmount } from 'utils/formatBalance'

const StyledCard = styled(Card)`
  align-self: center;
  display:block;
  width:360px;
  margin:10px;
`

const FarmCardInnerContainer = styled(Flex)`
  flex-direction: column;
  justify-content: space-around;
  
  padding: 24px;
`

const ExpandingWrapper = styled.div`
  padding: 24px;
  border-top: 2px solid ${({ theme }) => theme.colors.cardBorder};
  overflow: hidden;
`
class NFTModal{
	image="";

	description="";

	name="";

	id="";
}

const ViewNFTs: React.FC = () => {
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const { toastError, toastSuccess } = useToast()
  const [mystate, setMystate] = useState<any>()
  const hist=useHistory();
  
  const wstakingcontract=useNftStakingContract(getNftStakingcraneAddress());

  

  useEffect(()=>{
    
      

    async function loadData(){
      const contract = getDNFTContract(getDNFTcraneAddress());
    
      const gmarketcontract = getNftStakingContract(getNftStakingcraneAddress());
      if(gmarketcontract&&account)
      {
        const res=await gmarketcontract.getList(account)
        console.log("sad ",res)
        const alldata=[];
        for(let i=0;i<res.length;i++)
        {
          console.log("sads ",res)
            const uri=await contract.tokenURI(res[i].tokenId);
            console.log(uri)
            try{
              const resp= await axios.get("https://ipfs.io/ipfs/".concat("".concat(uri).substring(7)),{
                headers: {
                    'content-type': 'application/json'
                }
            });
            alldata.push(<StyledCard key={i} isActive={!false}>
              <FarmCardInnerContainer>
              <Flex justifyContent="space-between">
                  <Text>NFT Id:</Text>
                  <Text bold>{Number(res[i].tokenId)}</Text>
                </Flex>
                <br/>
                <Flex justifyContent="space-between">
                    <Text>Description:</Text>
                    <Text bold>{resp.data.description}</Text>
                  </Flex>
                  <br/>
                  <img style={{margin:"auto",width:"300px",height:"auto"}} alt="Nft avatar" src={"https://ipfs.io/ipfs/".concat("".concat(resp.data.image).substring(7))}/>
                  <br/>
                <Flex justifyContent="space-between">
                <Button onClick={
                  async ()=>{
                    try {

                      const tx = await wstakingcontract.unstake(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Unstaked'), t('NFT is unstaked from the pool'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Unstake</Button>
                <Button onClick={
                  async()=>{
                    try {

                      const tx = await wstakingcontract.harvest(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Harvested'), t('Reward is harvested and sent to your wallet'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Harvest</Button>
                  
                </Flex>
                
              </FarmCardInnerContainer>
        
              
            </StyledCard>);
          }
          catch(e){
            const resp={nam:"N/A",desc:"N/A",img:"N/A"};
            alldata.push(<StyledCard key={i} isActive={!false}>
              <FarmCardInnerContainer>
                
              <Flex justifyContent="space-between">
                  <Text>NFT Id:</Text>
                  <Text bold>{Number(res[i].tokenId)}</Text>
                </Flex>
               
                <br/>
                <Flex justifyContent="space-between">
                  <Text>Desc:</Text>
                  <Text bold>{resp.desc}</Text>
                </Flex>
                <br/>
                <img style={{margin:"auto",width:"400px",height:"auto"}} alt="Nft avatar" src="/logo.png"/>
                  <br/>
               
                <br/>
                
                <Flex justifyContent="space-between">
                <Button onClick={
                  async ()=>{
                    try {

                      const tx = await wstakingcontract.unstake(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Unstaked'), t('NFT is unstaked from the pool'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Unstake</Button>
                <Button onClick={
                  async()=>{
                    try {
                        console.log(Number(res[i].tokenId))
                      const tx = await wstakingcontract.harvest(res[i].tokenId);
                       const receipt=await tx.wait()
                        if (receipt.status) {
                          toastSuccess(t('Harvested'), t('Reward is harvested and sent to your wallet'))
                          
                        }
                      } catch (error) {
                        toastError(t('Error'), t('You are not allowed.'))
                        
                      
                      }
                  }
                }>Harvest</Button>
                </Flex>
              </FarmCardInnerContainer>
            </StyledCard>);
          }
            
            
        }
         setMystate(alldata);
      }
    }
    loadData();
  },[t,toastSuccess,hist,wstakingcontract,account,toastError])

 

  

  return (
    <>
      <PageHeader>
        <Heading as="h1" scale="xxl" color="secondary" mb="24px">
          {t('NFT Staking')}
        </Heading>
        <Heading scale="lg" color="text">
          {t('Stake your NFTs and earn our tokens')}
        </Heading>
        
      </PageHeader>
      <Page style={{textAlign:"center",display:"flex",flexWrap:"wrap"}}>
      
        {mystate}
        
      </Page>
    </>
  )
}

export default ViewNFTs
